<template>
  <div class="app">
    <LeftPanel :currentFun="currentFun" />
    <RightPanel :updateFun="updateFun" />
  </div>
</template>

<script>
import { ref } from 'vue';
import LeftPanel from './components/LeftPanel.vue';
import RightPanel from './components/RightPanel.vue';

export default {
  name: 'App',
  components: {
    LeftPanel,
    RightPanel
  },
  setup() {
    const currentFun = ref(50); // Начальный процент fun

    // Обновление процента в родительском компоненте
    const updateFun = (newFun) => {
      currentFun.value = newFun;
    };

    return { currentFun, updateFun };
  }
};
</script>


<style>
* {
  font-family: 'Rubik', sans-serif;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%; 
  margin: 0;
}

#app {
  height: 100%; 
}

.app {
  display: flex;
  height: 100%; 
}

@media (max-width: 768px) {
  .app {
    flex-direction: column;
  }
}

</style>
