<script>
export default {
  name: "LeftPanel",
  props: {
    // Пропс для текущего прогресса (процентов)
    currentFun: {
      type: Number,  // Тип пропса — число
      required: true
    }
  },
  computed: {
    // Создаем computed свойство, чтобы отформатировать прогресс до одного знака после запятой
    formattedFun() {
      return this.currentFun.toFixed(0); // Округляем до одного знака после запятой
    }
  }
};
