<template>
  <div class="right-panel">
    <div class="preferences-info">
      <div class="preferences-info__title">
        <p class="preferences-text">Ok, let’s change your preferences</p>
        <p class="preferences-text__down">To unsubscribe, please uncheck the appropriate box(es).</p>
      </div>
      <button class="unsubscribe-button" @click="openPopup">Unsubscribe from all</button>
    </div>
    <div class="subscription-container">
      <SubscriptionCard
        v-for="(site, index) in sites"
        :key="index"
        :site="site"
        @toggle="toggleSubscription"
      />
    </div>

    <!-- Popup Component -->
    <Popup v-if="showPopup" @close="closePopup" :old-fun="oldFun" :new-fun="newFun" @affirmative="resetSubscriptions"/>
  </div>
</template>

<script>
import { ref, watchEffect } from 'vue';
import SubscriptionCard from './SubscriptionCard/SubscriptionCard.vue';
import Popup from './Popup/Popup.vue'; 
import trainyrbrain from "../assets/SubscriptionCard/image.svg";
import ynquiz from "../assets/SubscriptionCard/image (1).svg";
import finditquiz from "../assets/SubscriptionCard/image (2).svg";
import moviequizonline from "../assets/SubscriptionCard/image (3).svg";
import findsuperstar from "../assets/SubscriptionCard/image (4).svg";
import whatcountry from "../assets/SubscriptionCard/image (5).svg";

export default {
  name: 'RightPanel',
  components: {
    SubscriptionCard,
    Popup
  },
  props: {
    updateFun: {
      type: Function,
      required: true
    }
  },
  setup(props) {
    const sites = ref([
      { siteName: 'Test your world geography skills', imageUrl: trainyrbrain, subscribed: true },
      { siteName: 'Test your knowledge with our fun and challenging quiz game! Compete, learn, and improve with a wide range of topics.', imageUrl: ynquiz, subscribed: true },
      { siteName: 'Guess the song from short clips', imageUrl: finditquiz, subscribed: false },
      { siteName: 'Identify films from iconic scenes', imageUrl: moviequizonline, subscribed: true },
      { siteName: 'Challenge your historical knowledge', imageUrl: findsuperstar, subscribed: false },
      { siteName: 'Answer questions on various sports', imageUrl: whatcountry, subscribed: false },
    ]);

    const oldFun = ref(50);  // Изначальный уровень fun
    const newFun = ref(50);  // Новый уровень fun после изменений
    const showPopup = ref(false);  // Показывать или скрывать попап

    // Рассчитываем новый прогресс FUN
    const calculateFun = () => {
      const subscribedCount = sites.value.filter(site => site.subscribed).length;
      newFun.value = Math.min(100, Math.max(0, (subscribedCount / sites.value.length) * 100));
      props.updateFun(newFun.value); // Обновляем прогресс в родительском компоненте
    };

    // Изменение состояния подписки
    const toggleSubscription = (siteName) => {
      const site = sites.value.find(s => s.siteName === siteName);
      if (site) {
        site.subscribed = !site.subscribed;
        calculateFun();
      }

      // Выводим весь массив сайтов в консоль
      console.log("Updated sites array:", JSON.stringify(sites.value, null, 2));
    };

    // Открытие попапа
    const openPopup = () => {
      oldFun.value = newFun.value;
      showPopup.value = true;
    };

    // Закрытие попапа
    const closePopup = () => {
      showPopup.value = false;
    };

    // Сброс подписок
    const resetSubscriptions = () => {
      sites.value.forEach(site => site.subscribed = false); // Сбрасываем все подписки
      calculateFun();  // Пересчитываем процент
      console.log("Updated subscribe.json", JSON.stringify(sites.value, null, 2)); // Логируем обновленные данные
      closePopup(); // Закрываем попап
    };

    // Слежение за изменениями
    watchEffect(() => {
      calculateFun();
    });

    return {
      sites,
      toggleSubscription,
      openPopup,
      closePopup,
      showPopup,
      oldFun,
      newFun,
      resetSubscriptions
    };
  }
};
</script>

<style scoped>
.right-panel {
  padding: 128px;
  background-color: #F1F1F1;
}

.preferences-info {
  display: flex;
  justify-content: space-between;

  margin-bottom: 24px;
}

.preferences-info__title{
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.preferences-text {
  font-size: 24px;
  color: #000000;
  line-height: 30px;
}

.preferences-text__down {
  font-size: 16px;
  color: #666666;
  line-height: 24px;
}

.unsubscribe-button {
  padding: 10px 16px;
  border: 1px solid #CCCCCC;
  color: #346DF1;
  border-radius: 4px;
  cursor: pointer;
  height: 36px;
  font-size: 16px;
  text-align: center;
  font-weight: 500;
}

.unsubscribe-button:hover {}

.subscription-container {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;

}

.subscription-card {
  width: calc(25% - 12px);
  box-sizing: border-box;
}


@media (max-width: 1400px) {
  .subscription-card {
    width: calc(33.33% - 12px); /* Три карточки в ряду */
  }

  .preferences-info__title{
    gap: 16px;
  }

  .preferences-text{
    font-size: 18px;
    line-height: 26px;
  }

  .preferences-text__down{
    font-size: 14px;
    line-height: 26px;
  }
  
  .unsubscribe-button{
    font-size: 12px;
    padding: 8px 12px;
  }

}

@media (max-width: 1024px) {

  .right-panel {
    padding: 64px;
    background-color: #F1F1F1;

}

  .subscription-card {
    width: calc(50% - 12px);
    padding: 14px;
  }

  .preferences-info{
    flex-direction: column;
    gap: 10px;
  }

  .preferences-info__title{
    gap: 10px;
  }
}

@media (max-width: 768px) {
  .subscription-card {
    width: calc(100% - 12px); /* Одна карточка в ряду */

  }
}

@media (max-width: 480px) {
    .right-panel{
        padding: 24px;
    }
}
</style>
