<template>
  <div class="subscription-card">
    <!-- Картинка сайта -->
    <div class="subscription-image">
      <img :src="site.imageUrl" alt="site.siteName" />
    </div>

    <!-- Информация о подписке -->
    <div class="subscription-info">
      <h3>{{ site.siteName }}</h3>
      <button 
        :class="['btn', { 'subscribed': site.subscribed, 'unsubscribed': !site.subscribed }]" 
        @click="toggleSubscription"
        :style="{
          padding: site.subscribed ? '4px 12px 4px 4px' : '4px 12px'
        }"
      >
        <!-- Иконка и текст, если подписка активна -->
        <template v-if="site.subscribed">
          <img src="../../assets/icon.svg" alt="checked-icon" class="icon" />
        </template>
        <span :style="site.subscribed ? { color: '#346DF1' } : {}">
          {{ site.subscribed ? 'Subscribe' : 'Subscribe' }}
        </span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SubscriptionCard',
  props: {
    site: {
      type: Object,
      required: true
    }
  },
  methods: {
    toggleSubscription() {
      // Меняем статус подписки
      this.$emit('toggle', this.site.siteName);
    }
  }
};
</script>

<style scoped>
.subscription-card {
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  gap: 16px;
  padding: 24px;
  background-color: #FFFFFF;

  box-sizing: border-box;
}

.subscription-image {
  width: 100%;
  height: 100%;
}

.subscription-image img {
  width: 100%;
  border-radius: 8px;
}

.subscription-info {
  display: flex;
  flex-direction: column;
  gap: 16px;
  text-align: center;
}

.subscription-info h3 {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #333333;
  text-align: left;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 48px;
}

.btn {
  width: fit-content;
  height: 32px;
  border: none;
  border-radius: 16px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  transition: background-color 0.3s ease, transform 0.3s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.subscribed {
  background-color: #ECF4FF;
  color:#346DF1;
}

.subscribed .icon {
  padding: 0 8px 0 0;
  width: 24px;
  height: 24px;
}

.unsubscribed {
  background-color: #346DF1;
  color:#FFFFFF;
}

/* Эффект увеличения кнопки при наведении */
.btn:hover {
  transform: scale(1.05);
}

@media (max-width: 1400px) {
    .subscription-info h3{
        font-size: 14px;
    }

}

@media (max-width: 1024px) {
    .btn{
        font-size: 12px;
    }
    .icon{
    width: 14px;
    height: 14px;
    }
}

@media (max-width: 768px) {
    .subscription-info h3{
        font-size: 18px;
        line-height: 24px;
    }
    .btn{
        font-size: 16px;
    }
    .icon{
        width: 24px;
        height: 24px;
    }
}




</style>