<template>
  <div class="popup-overlay" @click="closePopup">
    <div class="popup" @click.stop>
      <div class="popup-header">
        <img src="../../assets/close-icon.svg" class="close-btn" @click="closePopup" alt="Close">
        <h3>Are you sure?</h3>
      </div>
      <div class="popup-body">
        Please confirm that you want to unsubscribe from all and lose {{ formattedFun }}% fun.
      </div>
      <div class="popup-footer">
        <button @click="affirmativeAction">Affirmative</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Popup',
  props: {
    oldFun: {
      type: Number,
      required: true
    },
    newFun: {
      type: Number,
      required: true
    }
  },
  computed: {
    // Форматируем процент с одним десятичным знаком
    formattedFun() {
      return this.oldFun.toFixed(1);
    }
  },
  methods: {
    // Закрытие попапа без сброса
    closePopup() {
      this.$emit('close');  // Сигнализируем родительскому компоненту, что попап закрыт
    },
    // Подтверждение действия (сбрасывает все подписки)
    affirmativeAction() {
      this.$emit('affirmative');  // Сигнализируем родительскому компоненту, чтобы сбросить все подписки
      this.closePopup();  // Закрыть попап
    }
  }
};
</script>

<style scoped>
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(12, 28, 96, 0.3); 
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
  backdrop-filter: blur(8px);
}

.popup {
  background-color: white;
  border-radius: 8px;
  width: 512px;
  text-align: center;
  position: relative;
}

.popup-header {
    display: flex;
    justify-content: start;
    align-items: center;
    padding-left: 24px;
  height: 68px;
  position: relative;

}

.popup-header h3 {
  margin: 0;
  font-size: 20px;
  font-weight: 500;
  color: #0C1C60;
}

.close-btn {
  position: absolute;
  top: 16px;
  right: 16px;
  cursor: pointer;
}

.popup-body {
  font-size: 16px;
  padding: 0 24px;
  text-align: left;
  color: #666666;
}

.popup-footer {
  display: flex;
  justify-content: end;
  padding: 24px;
}

.popup-footer button {
  padding: 10px 16px;
  border: none;
  border-radius: 4px;
  background-color: #346DF1;
  color: #FFFFFF;
  cursor: pointer;
}

.popup-footer button:hover {
  background-color: #2858c7;
}
</style>